<template>
  <div class="editor-quill">
    <div ref="editor" class="editor" :style="style"></div>
  </div>
</template>

<script >
  import { computed, defineComponent, onMounted, ref, watch } from "vue";
  import Quill from "quill";
  import "quill/dist/quill.snow.css";

  export default defineComponent({
    name: "editor-quill",
    props: {
      options: Object,
      modelValue: null,
      height: {
        type:[String, Number],
        default:'400px'
      },
      width: [String, Number]
    },
    emits: ["update:modelValue", "load"],
    setup(props, { emit }) {

      let quill = null;
      const editor = ref(null);
      const upload = ref(null);

      // 
      const content = ref("");

      // 
      const cursorIndex = ref(0);

      // 
      function uploadFileHandler() {
        const selection = quill.getSelection();

        if (selection) {
          cursorIndex.value = selection.index;
        }
        emit("upload-open");
      }

      // 
      function setUploadFiles(files) {
        if (files.length > 0) {
          // 
          files.forEach((file, i) => {
            const type = file.type || 'image';

            quill.insertEmbed(cursorIndex.value + i, type, file.url, Quill.sources.USER);
          });

          // 
          quill.setSelection(cursorIndex.value + files.length);
        }
      }

      // 
      function setContent(val) {
        quill.root.innerHTML = val || "";
      }

      // 
      const style = computed(() => {
        const height = typeof props.height  === 'number' ? props.height + "px" : props.height;
        const width = typeof props.width === 'number' ? props.width + "px" : props.width;

        return {
          height,
          width
        };
      });

      // 
      watch(
        () => props.modelValue,
        (val) => {
          if (val) {
            if (val !== content.value) {
              setContent(val);
            }
          } else {
            setContent("");
          }
        }
      );

      onMounted(function () {
        console.log('---------editer',editor)
        // 
        quill = new Quill(editor.value, {
          theme: "snow",
          placeholder: "",
          modules: {
            toolbar: [
              ["bold", "italic", "underline", "strike"],
              ["blockquote", "code-block"],
              [{ header: 1 }, { header: 2 }],
              [{ list: "ordered" }, { list: "bullet" }],
              [{ script: "sub" }, { script: "super" }],
              [{ indent: "-1" }, { indent: "+1" }],
              [{ direction: "rtl" }],
              [{ size: ["small", false, "large", "huge"] }],
              [{ header: [1, 2, 3, 4, 5, 6, false] }],
              [{ color: [] }, { background: [] }],
              [{ font: [] }],
              [{ align: [] }],
              ["clean"],
              ["link", "image"]
            ]
          },
          ...props.options
        });

        // 
        quill.getModule("toolbar").addHandler("image", uploadFileHandler);

        // 
        quill.on("text-change", () => {
          content.value = quill.root.innerHTML;
          emit("update:modelValue", content.value);
        });

        // 
        setContent(props.modelValue);

        // 
        emit("load", quill);
      });

      return {
        content,
        editor,
        upload,
        cursorIndex,
        style,
        setContent,
        setUploadFiles
      };
    }
  });
</script>

