<template>
  <default-field
    v-model="value"
    v-bind="labelProps"
    @reset="resetField"
    ref="def"
  >
    <el-input v-model="value" placeholder="">
      <template #prepend>
        <quick-icon :icon="value" />
      </template>
      <template #append>
        <select-icon @selected="selectIcon"></select-icon>
      </template>
    </el-input>

  </default-field>
</template>

<script>
  import {computed, ref, useAttrs, onMounted} from "vue";
  import {baseProps} from '../Composition/FormField';

export default {
  name: 'IconField',
  props: {
    ...baseProps,
    marks: {
      type: Object,
      default: () => {
      }
    }
  },
  setup(props) {

    const attrs = useAttrs();
    const value = ref('');
    const def = ref(null);

    /**
     * label
     */
    const labelProps = computed(() => {
      return {
        ...props,
        ...attrs
      };
    });


    /**  **/
    const resetField = (data) => {

      if (data) {
        value.value = data;
        return;
      }
      value.value = initialValue();
    };

    /**
     * 
     */
    const initialValue = () => {
      return !(props.default === undefined || props.default === null)
        ? props.default
        : '';
    };


    /**  start ******/
    const validate = () => {
      return false;
    };

    const selectIcon = (event) => {
      value.value = event
    }

    onMounted(() => {
      value.value = initialValue();
    })


    return {
      resetField,
      validate,
      labelProps,
      selectIcon,
      attrs,
      value,
      def,
    }
  }
}
</script>
