<template>
  <default-field
    v-model="value"
    v-bind="labelProps"
    @reset="resetField"
    ref="def"
  >
    <el-input-number
      v-model="value"
      v-bind="fieldAttrs"
      @blur="handleBlur"
    />
  </default-field>
</template>

<script>

  import {computed, ref, useAttrs,onMounted} from "vue";
  import { baseProps } from '../Composition/FormField';

  export default {
    name: 'InputNumberField',
    props: {
      ...baseProps
    },
    setup(props) {

      const attrs = useAttrs();
      const value = ref('');
      const def = ref(null);

      const fieldAttrs = computed(() => {
        return {
          ...attrs,
          disabled: props.disabled,
        };
      });

      /**
       * label
       */
      const labelProps = computed(() => {
        return {
          ...props,
          ...attrs
        };
      });


      /**  start ******/
      const validate = () => {
        return false;
      };

      /**  **/
      const resetField = (data) => {

        if (data) {
          value.value = data;
          return;
        }
        value.value = initialValue();
      };

      /**
       * 
       */
      const initialValue = () => {
        return !(props.default === undefined || props.default === null)
          ? props.default
          : '';
      };


      onMounted(() => {
        value.value = initialValue();
      })

      /**
       * blur 
       */
      const handleBlur = () => {
        def.value.validate('blur');
      };


      return {
        handleBlur,
        resetField,
        validate,
        labelProps,
        fieldAttrs,
        value,
        def,
      }
    },
  }

</script>
<style scoped lang="scss">
  :deep .el-select {
    width: 100%;
  }
</style>
